<template>
  <div>     
    <create-party v-on:add-party="createParty"/>
    <v-container class="mb-15">
      <p
        class="font-weight-light"
        v-if="parties.length <= 0"
      >Er bestaan nog geen huizen, voeg ze hier toe.</p>

      <v-simple-table v-if="parties.length > 0">
        <template v-slot:default>
          <thead>
            <tr>
              <th class="text-left v-data-table__divider">Naam</th>
              <th class="text-left v-data-table__divider">Datum</th>
              <th class="text-left v-data-table__divider">Open</th>
              <th class="text-left v-data-table__divider"></th>
              <th class="text-left v-data-table__divider">Bewerk status</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="party in parties" :key="party._id">
              <td class="v-data-table__divider">{{ party.name }}</td>
              <td class="v-data-table__divider">{{ party.date  || formatDate}}</td>
              <td class="v-data-table__divider" v-if="party.open">Open</td>
              <td class="v-data-table__divider" v-if="!party.open">Afgelopen</td>
            <td  class="v-data-table__divider" style="width:12%;">
                <v-btn @click="selectParty(party._id)">
                    Selecteer
                </v-btn>
            </td>
            <td v-if="!party.open" class="v-data-table__divider" style="width:12%;">
                <v-btn @click="changePartyStatus(party._id)">
                    Open het feest
                </v-btn>
            </td>
            <td v-if="party.open" class="v-data-table__divider" style="width:12%;">
                <v-btn @click="changePartyStatus(party._id)">
                    Sluit het feest
                </v-btn>
            </td>
            </tr>
          </tbody>
        </template>
      </v-simple-table>
    </v-container>
  </div>
</template>

<script>
import moment from "moment";
import Vue from "vue";

Vue.filter("formatDate", function(value) {
  if (value) {
    return moment(String(value)).format("L");
  }
});

import CreateParty from '../components/CreateParty.vue';
export default {
  components: { CreateParty },
  data() {
    return {
      houseId: null,
      parties: []
    };
  },
  methods: {
      selectParty(id) {
            this.$router.push(id)
      },
      async changePartyStatus(id) {
        await this.$http.post('https://ss15-node.herokuapp.com/api/party/status/' + id)
        .then(res => console.log(res.data))

        await this.$http
      .get("https://ss15-node.herokuapp.com/api/party/" + this.$route.params.id)
      .then(response => (this.parties = response.data));
      },
      createParty(object) {
      this.$http
        .post("https://ss15-node.herokuapp.com/api/party/" + this.houseId, object)
        .then(() => {
          this.$http
          .get("https://ss15-node.herokuapp.com/api/party/" + this.$route.params.id)
          .then(response => (this.parties = response.data));
        })
        .catch(() => {})
        .finally(() => {
          //Perform action in always
        });
    },
  },
  async created() {
    this.houseId = await this.$route.params.id;

    await this.$http
      .get("https://ss15-node.herokuapp.com/api/party/" + this.$route.params.id)
      .then(response => (this.parties = response.data));

      console.log(this.parties)
  }
};
</script>

