<template>
  <div class="text-right">
      <v-card>
        <v-card-title class="headline grey yippee white--text" primary-title>Join feest</v-card-title>
        <v-card-text>
          <v-form class="px-3" v-model="valid">
            <v-text-field
              v-model="firstname"
              label="Voornaam"
              prepend-icon="folder"
              :rules="[rules.required]">
            ></v-text-field>

            <v-text-field
              v-model="lastname"
              label="Achternaam"
              prepend-icon="folder"
              :rules="[rules.required]">
            ></v-text-field>

            <v-text-field
              v-model="phonenumber"
              label="Telefoon contactpersoon"
              prepend-icon="folder"
              type="number"
              :rules="[rules.required, rules.validatePhone]">
            ></v-text-field>


            <v-btn tile block class="yippee white--text mx-0 mt-3" @click="submit" 
            :disabled="
            (firstname == '' || firstname == null) ||
            (lastname == '' || lastname == null) ||
            (!valid)"
            >Voeg toe</v-btn>
          </v-form>
        </v-card-text>
      </v-card>
  </div>
</template>

<script>
export default {
  data: () => ({
    rules: {
        required: value => !!value || 'Dit veld is vereist.',
        counter: value => value.length <= 20 || 'Maximaal 20 tekens.',
        email: value => {
          /* eslint-disable no-useless-escape */
          const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
          return pattern.test(value) || 'Ongeldig email adres'
        },
        zipcode: value => {
          /* eslint-disable no-useless-escape */
          const pattern = /^[1-9][0-9]{3} ?(?!sa|sd|ss)[a-z]{2}$/i
          return pattern.test(value) || 'Ongeldig postcode, 1234AB is het formaat'
        },
        validatePhone: value => {
          var vast_nummer = /^(((0)[1-9]{2}[0-9][-]?[1-9][0-9]{5})|((\\+31|0|0031)[1-9][0-9][-]?[1-9][0-9]{6}))$/;
          var mobiel_nummer = /^(((\\+31|0|0031)6){1}[1-9]{1}[0-9]{7})$/i;
          return (vast_nummer.test(value) || mobiel_nummer.test(value));
      }
      },
    dialog: false,
    valid: false,
    requiredRules: [v => !!v || "Dit veld is verplicht"],
    errors: [],

    partyId: null,

    firstname: null,
    lastname: null,
    phonenumber: null
  }),
  methods: {
    print() {
        window.print();
      },
    submit() {
      const object = {
        firstname: this.firstname,
        lastname: this.lastname,
        phonenumber: this.phonenumber
      };
      this.firstname = null,
      this.lastname = null
      this.phonenumber = null

      JSON.stringify(object);

      this.$http.post('https://ss15-node.herokuapp.com/' + this.partyId, object)
      .then(res => console.log(res.data))

      history.pushState(null, null, location.href);
      window.onpopstate = function () {
          history.go(1);
      };
      this.$router.push('/thanks')
    },
  },
  created() {
    this.partyId = this.$route.params.id;
  }
};
</script>
