<template>
  <div class="text-right">
    <v-dialog width="500" v-model="dialog">
      <template v-slot:activator="{ on }">
        <v-btn tile class="mr-1 checkpointbg white--text" dark v-on="on">Nieuw huis</v-btn>
      </template>
      <v-card>
        <v-card-title class="headline grey checkpointbg white--text" primary-title>Voeg huis toe</v-card-title>
        <v-card-text>
          <v-form class="px-3" v-model="valid">
            <v-text-field
              v-model="housename"
              label="Naam"
              prepend-icon="folder"
              required
            ></v-text-field>

            <v-text-field
              v-model="street"
              label="Straat"
              prepend-icon="folder"
              required
            ></v-text-field>

            <v-text-field
              v-model="housenumber"
              label="Huisnummer"
              prepend-icon="folder"
              type="number"
              required
            ></v-text-field>

            <v-btn tile block class="yippee white--text mx-0 mt-3" @click="submit" 
            :disabled="(!valid)">Voeg toe</v-btn>
          </v-form>
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
  data: () => ({
    dialog: false,
    valid: false,
    requiredRules: [v => !!v || "Dit veld is verplicht"],
    errors: [],

    housename: null,
    street: null,
    housenumber: null,
  }),
  methods: {
      partyDate(date){
      this.date = date;
    },
    print() {
        window.print();
      },
      
    submit() {
      const object = {
        name: this.housename,
        street: this.street,
        housenumber: this.housenumber
      };
      this.housename = "",
      this.street = ""
      this.housenumber = ""

      this.dialog = false;
      JSON.stringify(object);
      this.$emit("add-house", object);
    },
  }
};
</script>
