<template>
<div>
  <v-container class="">
  <div>
  <h1>detailpagina van {{party.name}}</h1>
<VueQRCodeComponent :text="qrcode"></VueQRCodeComponent>
<v-row>
  <v-col>
  <div v-if="party.open">
    <p>http://192.168.1.87:8080/join/party/{{partyId}}</p>
  <v-btn :to="'/join/party/' + party._id" target="_blank">
    Join
  </v-btn>
  </div>
  <div v-if="!party.open">
    <p>Het feest is momenteel gesloten voor aanmeldingen.</p>
  </div>

  </v-col>
  <v-col>
    <v-btn v-if="party.open" @click="changePartyStatus(party._id)">
      Sluit het feest
    </v-btn>
    <v-btn v-if="!party.open"  @click="changePartyStatus(party._id)">
      Open het feest
    </v-btn>
  </v-col>
</v-row>
</div>
    <v-row no-gutters>
      <v-col
        cols="12"
        sm="6"
      >
        <v-card
          class="ma-2 pa-2"
          outlined
          tile
        >
        <v-card-title>
          <v-col class="ma-0 pa-0" cols="10">
          Bezoekers
            
          </v-col>
          <v-col class="ma-0 pa-0">
            {{attendants.length}}
          </v-col>

        </v-card-title>
        <v-card-text v-if="attendants.length == 0">
          Er zijn nog geen aanmeldingen
        </v-card-text>
         <v-simple-table v-if="attendants.length > 0">
          <template v-slot:default>
            <thead>
              <tr>
                <th class="text-left">
                  Naam
                </th>
                <th class="text-left">
                  Telefoon
                </th>
                <th>
                  <v-icon class="white--text">dashboard</v-icon>
                </th>
              </tr>
            </thead>
            <tbody>
            <tr v-for="person in attendants" :key="person._id">
              <td>{{ person.firstname }} {{person.lastname}}</td>
              <td>{{ person.phonenumber }}</td>
              <td>
                <v-btn @click="deleteUser(person._id)">delete</v-btn>
              </td>

            </tr>
          </tbody>
          </template>
        </v-simple-table>
        </v-card>
      </v-col>

      <v-col
        cols="12"
        sm="6"
      >
        <v-card
          class="ma-2 pa-2"
          outlined
          tile
        >
        <v-card-title>
          <v-col class="ma-0 pa-0" cols="9">
          Kosten
            
          </v-col>
          <v-col class="ma-0 pa-0">
            165,-
          </v-col>

        </v-card-title>

         <v-simple-table>
          <template v-slot:default>
            <thead>
              <tr>
                <th class="text-left">
                  Product
                </th>
                <th class="text-left">
                  Gekocht
                </th>
                <th class="text-left">
                  Soort
                </th>
                <th class="text-left">
                  Prijs
                </th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>2 kratten bier</td>
                <td>Dries van Esch</td>
                <td>Bier</td>
                <td>30</td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</div>
</template>

<script>
import VueQRCodeComponent from 'vue-qrcode-component'

export default {
  components: {
    VueQRCodeComponent
  },
    data: () => ({
        qrcode: null,
        partyId: null,
        party: {},
        attendants: []
    }),
    methods: {
      async changePartyStatus(id) {
        await this.$http.post('/api/party/status/' + id)
        .then(res => console.log(res.data))

        await this.$http.get('https://ss15-node.herokuapp.com/api/party/one/' + this.partyId)
        .then(res => this.party = res.data)

        await this.$http.get('https://ss15-node.herokuapp.com/join/party/' + this.partyId)
        .then(res => this.attendants = res.data)
      },
      joinParty(id) {
        this.$router.push('/join/party/' + id)
      },
      async deleteUser(userId) {
        await this.$http.delete('/join/party/' + userId)

        this.$http.get('https://ss15-node.herokuapp.com/api/party/one/' + this.partyId)
        .then(res => this.party = res.data)

        await this.$http.get('https://ss15-node.herokuapp.com/join/party/' + this.partyId)
        .then(res => this.attendants = res.data)
      }
    },
    async created() {
        this.partyId = await this.$route.params.partyId

        await this.$http.get('https://ss15-node.herokuapp.com/api/party/one/' + this.partyId)
        .then(res => this.party = res.data)

        await this.$http.get('https://ss15-node.herokuapp.com/join/party/' + this.partyId)
        .then(res => this.attendants = res.data)

        this.qrcode = await "https://ss15-node.herokuapp.com/join/party/" + this.partyId;
    }

}
</script>

