<template>
  <v-app id="inspire" class="loginbg">
    <v-main>
      <v-container fluid fill-height>
        <v-layout align-top justify-center class="loginform">
          <v-flex xs12 sm8 md4>
            <v-card elevation="1">
              <v-toolbar dark color="yippee" height="100px" elevation="0">
                <v-img max-height="40" max-width="218"></v-img>
              </v-toolbar>
              <v-card-text>
                <v-form>
                  <h4 class="font-weight-medium">Login</h4>
                  <p class="body-2">
                    Vul hieronder uw gebruikersnaam en wachtwoord in om toegang
                    te verkrijgen tot Checkpoint-IC.
                  </p>
                  <v-text-field name="email" label="E-mailadres" type="text" v-model="email">
                    <p slot="prepend" style="width: 150px;" class="font-weight-light">E-mailadres:</p>
                  </v-text-field>

                  <v-text-field
                    id="password"
                    name="password"
                    label="Wachtwoord"
                    type="password"
                    v-model="password"
                  >
                    <p slot="prepend" style="width: 150px;" class="font-weight-light">Wachtwoord:</p>
                  </v-text-field>
                  <v-checkbox v-model="stayOnline">
                    <template #label>Vandaag ingelogd blijven</template>
                  </v-checkbox>
                  <p class="red--text" v-if="errors.length > 0">{{this.errors[0]}}</p>
                </v-form>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn
                  type="submit"
                  @click="handleSubmit"
                  block
                  class="yippee white--text mr-5 pa-2 mb-4"
                >Inloggen</v-btn>
              </v-card-actions>
              <v-card-text class="forgotpassword">
                <p class="font-weight-light ma-0 blue--text">Uw wachtwoord vergeten?</p>
              </v-card-text>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
export default {
  name: "Login",
  props: {
    source: String
  },
  data() {
    return {
      email: "",
      password: "",
      stayOnline: false,
      tokenExpiry: 0,
      errors: []
    };
  },
  created() {
    localStorage.removeItem("token");
    localStorage.removeItem("user");
  },
  methods: {
    handleSubmit(e) {
      if (this.stayOnline) {
        this.tokenExpiry = 12;
      }

      e.preventDefault();
      if (this.password.length > 0) {
        this.$http
          .post("https://ss15-node.herokuapp.com/api/auth/login", {
            email: this.email,
            password: this.password,
            expiryHours: this.tokenExpiry
          })
          .then(response => {
            localStorage.setItem("token", response.data.token);
            if (localStorage.getItem("token") != null) {
              this.$emit("loggedIn");
              this.$router.push("/houses");
            }
          })
          .catch(error => {
            this.loading = false;
            console.log(error.response);
            if (error.response.status == 400) {
              this.errors.push("Je gegevens lijken niet correct te zijn.");
              this.email = "";
              this.password = "";
            } else {
              this.errors.push("Er is iets misgegaan, ververs de pagina.");
            }
          });
      }
    }
  }
};
</script>

<style>
.loginform {
  padding-top: 5%;
}

.loginbg {
  background-color: #e6ece2 !important;
}

.forgotpassword {
  background-color: #f7f7f7;
}
</style>
