<template >
  <nav>
    <v-app-bar plain app class height="75px">
      <v-app-bar-nav-icon class="grey--text" @click="drawer = !drawer"></v-app-bar-nav-icon>
      <v-toolbar-title class="text-uppercase grey--text">
      </v-toolbar-title>
      <v-spacer></v-spacer>

      <v-menu offset-y>
        <template v-slot:activator="{ on, attrs }">
          <v-btn tile color="black" plain v-bind="attrs" v-on="on" @click="chooseHouse()">
            <v-icon>mdi-chevron-down</v-icon>
            <span>{{currentHouse.name}} - {{currentHouse.street}} {{currentHouse.housenumber}}</span>
          </v-btn>
        </template>

      </v-menu>

      <v-btn tile plain color="black" @click="logout">
        <span>Sign Out</span>
        <v-icon right>exit_to_app</v-icon>
      </v-btn>
    </v-app-bar>
    <v-navigation-drawer v-model="drawer" app class="yippee" width="250px">
       <v-row justify="center">
        <v-col cols="6">
          <v-badge bottom right color="blue" icon="admin_panel_settings" overlap>
              <v-avatar
                size="100"
                class="mt-5"
                style="outline: 4px solid white;"
              >
              <!-- <img src="/avatar-1.jpg" /> -->
              <span class="display-1 white--text text-decoration-none">
                {{
                    getInitials(
                    user.firstname +
                    " " +
                    user.lastname
                    )
                    }}
              </span>
              </v-avatar>
          </v-badge>
            <router-link to="/account" class="text-decoration-none">
          </router-link>
        </v-col>
      </v-row>

      <v-list>
        <v-list-item-action></v-list-item-action>
        <v-list-item v-for="link in links" :key="link.text" router :to="link.route">
          <v-list-item-action>
            <v-icon class="white--text">{{link.icon}}</v-icon>
          </v-list-item-action>
          <v-list-item-content>
            <v-list-item-title class="white--text">{{link.text}}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>
  </nav>
</template>

<script>
export default {
  components: {},
  data() {
    return {
      houses: [],
      houseId: null,
      currentHouse: {},
      user: {},
      drawer: true,
      links: [
        { 
          icon: "dashboard", 
          text: "Home", 
          route: "dashboard" 
        },
        { 
          icon: "sports_bar", 
          text: "Chillen", 
          route: 'party'
        },
        { 
          icon: "list", 
          text: "Lijsten", 
          route: 'lists'
        },
      ]
    };
  },
  methods: {
    async changeHouse(id){
      await this.$http
        .get("/api/house/mine/" + this.$route.params.id)
        .then(response => (this.houses = response.data));

      await this.$http
        .get('https://ss15-node.herokuapp.com/api/house/' + this.$route.params.id)
        .then(response => (this.currentHouse = response.data))

      this.$router.push('/' + id + '/lists')
    },
    chooseHouse(){
      this.$router.push('/houses')
    },
    getInitials(fullname) {
      var names = fullname.split(" "),
        initials = names[0].substring(0, 1).toUpperCase();

      if (names.length > 1) {
        initials += names[names.length - 1].substring(0, 1).toUpperCase();
      }
      return initials;
    },
    logout() {
      localStorage.removeItem("token");
      this.$router.push("/login");
    }
  },
  async created() {
    await this.$http.get('https://ss15-node.herokuapp.com/api/auth/')
    .then((res) => this.user = res.data)

    await this.$http
      .get("/api/house/mine/" + this.$route.params.id)
      .then(response => (this.houses = response.data));

    await this.$http
      .get('https://ss15-node.herokuapp.com/api/house/' + this.$route.params.id)
      .then(response => (this.currentHouse = response.data))

    
  }
};
</script>