<template>
  <h1>{{house.name}}</h1>
</template>

<script>
export default {
  data: () => ({
    message: null,
    house: {}
  }),
  created() {
    this.$http.get('https://ss15-node.herokuapp.com/api/house/' + this.$route.params.id)
    .then(res => this.house = res.data)
  },

}
</script>

