<template>
  <div class="text-right">
    <v-dialog width="500" v-model="dialog">
      <template v-slot:activator="{ on }">
        <v-btn tile class="mr-1 checkpointbg white--text" dark v-on="on">Nieuw feest</v-btn>
      </template>
      <v-card>
        <v-card-title class="headline grey checkpointbg white--text" primary-title>Voeg omgeving toe</v-card-title>
        <v-card-text>
          <v-form class="px-3" v-model="valid">
            <v-text-field
              v-model="name"
              label="Naam"
              prepend-icon="folder"
              required
            ></v-text-field>
            <CreateDatePicker label="Vanaf" icon="list" v-on:date-picker="partyDate" />

            <v-btn tile block class="checkpointbg white--text mx-0 mt-3" @click="submit" 
            :disabled="(name == '' || name == null)">Voeg toe</v-btn>
          </v-form>
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import CreateDatePicker from './CreateDatePicker'
export default {
    components: {
        CreateDatePicker
    },
  data: () => ({
    dialog: false,
    valid: false,
    requiredRules: [v => !!v || "Dit veld is verplicht"],
    errors: [],

    name: null,
    date: null,
  }),
  methods: {
      partyDate(date){
      this.date = date;
    },
    print() {
        window.print();
      },
      
    submit() {
      const object = {
        name: this.name,
        date: this.date
      };
      this.name = "",
      this.date = ""

      this.dialog = false;
      JSON.stringify(object);
      this.$emit("add-party", object);
    },
  }
};
</script>
