<template>
    <v-dialog
                ref="dialog"
                v-model="modal"
                :return-value.sync="date"
                persistent
                width="290px"
              >
                <template v-slot:activator="{ on }">
                  <v-text-field
                    :prepend-icon="icon"
                    v-model="date"
                    :label=label
                    readonly
                    v-on="on"
                  ></v-text-field>
                </template>
            <v-date-picker
                  v-model="date"
                  scrollable
                  locale="nl-NL"
                  :first-day-of-week="1">
                  <v-spacer></v-spacer>
                  <v-btn
                    class="white--text" 
                    color="checkpointbg"
                    @click="modal = false"
                  >
                    Ga terug
                  </v-btn>
                  <v-btn
                    class="white--text" 
                    color="checkpointbg"
                    @click="submit()"
                    
                  >
                    OK
                  </v-btn>
            </v-date-picker>
    </v-dialog>
</template>

<script>
import moment from "moment";
import Vue from "vue";

Vue.filter("formatDate", function(value) {
  if (value) {
    return moment(String(value)).format("DD/MM/YYYY");
  }
});

export default {
  props: {
    icon: null,
    label: null
  },
    data() {
        return {
          modal: false,
          date: null
        }
    },
    methods: {
    submit() {
      this.$refs.dialog.save(this.date);
      this.$emit("date-picker", this.date);
    },
  }

}
</script>

