<template>
  <VueQRCodeComponent :text="qrcode"></VueQRCodeComponent>
</template>

<script>
export default {
    data: () => ({
        partyId: null,
        qrcode: null
    }),
    async created() {
        this.partyId = await this.$route.params.partyId;

        this.qrcode = "https://ss15-node.herokuapp.com/join/party/" + this.partyId;
    }

}
</script>

