const routes = [
  {
    path: "/login",
    name: "Login",
    component: Login,
    meta: {
      hideNavigation: true,
    },
  },
  {
    path: "/houses",
    name: "Houses",
    component: Houses,
    meta: {
      hideNavigation: true,
    },
  },
  {
    path: "/:id/dashboard",
    name: "Dashboard",
    component: Dashboard,
    meta: {
      hideNavigation: false,
    },
  },
  {
    path: "/:id/lists",
    name: "Lists",
    component: Lists,
    meta: {
      hideNavigation: false,
    },
  },
  {
    path: "/:id/party",
    name: "Party",
    component: Party,
    meta: {
      hideNavigation: false,
    },
  },
  {
    path: "/:id/:partyId",
    name: "Detailpagina",
    component: PartyDetail,
    meta: {
      hideNavigation: false,
    },
  },
  {
    path: "/join/party/:id",
    name: "Join a party",
    component: JoinParty,
    meta: {
      hideNavigation: true,
    },
  },
  {
    path: "/:partyId/qr",
    name: "QR view",
    component: QrCodeView,
    meta: {
      hideNavigation: false,
    },
  },
  {
    path: "/thanks",
    name: "Thanks",
    component: ThanksPage,
    meta: {
      hideNavigation: true,
    },
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
